export function getNextUnsolvedTaskWithUnexpected(
	columns,
	tasks,
	solvedTasks,
	unexpectedTasks,
	startIndex = 0
  ) {
	// 1. Jeli wyszlimy poza zakres etapw  nie ma kolejnego zadania.
	if (startIndex >= columns.length) {
	  return null;
	}
  
	const currentStage = columns[startIndex];
  
	// ----------------------
	// KROK A: SPRAWD, CZY S GWNE ZADANIA W currentStage
	// ----------------------
	const mainTasks = tasks[currentStage] || [];
	const unsolvedMainTasks = mainTasks.filter(
	  (task) => !solvedTasks.some((st) => st.taskId === task.id)
	);
  
	// Jeeli w biecej kolumnie zostay jeszcze gwne zadania:
	// zwracamy pierwsze i koczymy (to jest priorytet #1)
	if (unsolvedMainTasks.length > 0) {
	  return unsolvedMainTasks[0];
	}
  
	// ----------------------
	// KROK B: SPRAWD, CZY S ZADANIA NIEPRZEWIDZIANE DO ROZWIZANIA W currentStage
	// ----------------------
	const currentStageIndex = columns.indexOf(currentStage);
  
	// Dla wygody sprawdzamy, czy wszystkie gwne zadania w "stage" danego 
	// zadania nieprzewidzianego zostay ukoczone  wtedy moemy uzna, 
	// e to zadanie "pojawio si" i jest moliwe do rozwizywania.
	const isStageFinished = (stageName) => {
	  const tasksInThatStage = tasks[stageName] || [];
	  const allMainDone = tasksInThatStage.every((t) =>
		solvedTasks.some((st) => st.taskId === t.id)
	  );
	  return allMainDone;
	};
  
	// Zadania do rozwizania w tym etapie to te, ktre:
	//  - Maj solveStage === currentStage (tutaj *rozwizujemy*),
	//  - Ich "bazowe" stage jest ju skoczone (lub rwne currentStage  zalenie od potrzeb).
	//    Najczciej chcesz: columns.indexOf(ut.stage) < currentStageIndex
	//    ALBO (ut.stage === currentStage) => wtedy zadanie pojawio si w trakcie 
	//    i te chcemy je tu rozwiza po gwnych zadaniach.
	const unexpectedTasksForSolveStage = unexpectedTasks.filter((ut) => {
	  // Sprawdzamy, czy w ogle "chce" by rozwizane w tym etapie:
	  if (ut.solveStage !== currentStage) {
		return false;
	  }
  
	  // Czy etap, w ktrym to zadanie si "pojawia", jest ju ukoczony
	  // (lub jest wanie tym etapem)? 
	  // Jeeli chcesz, by zadanie mogo si rozwizywa w tym samym momencie, 
	  // co jest jego stage, uyj <=. Jeeli dopiero w kolejnym  uyj <.
	  const utStageIndex = columns.indexOf(ut.stage);
	  // Zakadam tu, e wystarczy utStageIndex <= currentStageIndex
	  // i jednoczenie "main tasks" w ut.stage s solved.
	  const canBeSolvedNow =
		utStageIndex <= currentStageIndex && isStageFinished(ut.stage);
  
	  return canBeSolvedNow;
	});
  
	// Teraz bierzemy z nich takie, ktre jeszcze nie s solved:
	const unsolvedUnexpected = unexpectedTasksForSolveStage.filter(
	  (ut) => !solvedTasks.some((st) => st.taskId === ut.id)
	);
  
	// Jeeli mamy jeszcze zadania nieprzewidziane w tym etapie  zwr pierwsze
	if (unsolvedUnexpected.length > 0) {
	  return unsolvedUnexpected[0];
	}
  
	// ----------------------
	// KROK C: JELI TUTAJ TRAFIMY, 
	// to znaczy, e w aktualnym etapie nie ma ju zada 
	// (ani gwnych, ani nieprzewidzianych do rozwizania).
	// Przechodzimy do kolejnego etapu.
	// ----------------------
	return getNextUnsolvedTaskWithUnexpected(
	  columns,
	  tasks,
	  solvedTasks,
	  unexpectedTasks,
	  startIndex + 1
	);
  }

  export function calculateCost(cost) {
	if (typeof cost === 'number') return cost;
  
	const parts = cost.split('+');
	let total = 0;
  
	for (const part of parts) {
	  if (part.includes('k6')) {
		const multiplier = parseInt(part.replace('k6', ''), 10) || 1;
		total += multiplier * Math.floor(Math.random() * 6 + 1);
	  } else {
		total += parseInt(part, 10);
	  }
	}
  
	return total;
  }