import React from "react";
import { Box, Button, Typography, Divider } from "@mui/material";

const StartScreen = ({ onStart }) => {
  return (
    <Box
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        height: "100vh",
        background: "linear-gradient(135deg, #6a11cb 0%, #2575fc 100%)",
        color: "#fff",
        textAlign: "center",
        padding: "20px",
      }}
    >
      <Typography
        variant="h3"
        gutterBottom
        style={{
          fontWeight: "bold",
          textShadow: "2px 2px 4px rgba(0, 0, 0, 0.5)",
        }}
      >
        Witaj w Grze!
      </Typography>

      {/* Sekcja: Opis gry */}
      <Box style={{ maxWidth: "800px", marginBottom: "20px" }}>
        <Typography
          variant="h5"
          gutterBottom
          style={{
            fontWeight: "bold",
            marginBottom: "10px",
            textShadow: "1px 1px 3px rgba(0, 0, 0, 0.3)",
          }}
        >
          Opis gry
        </Typography>
        <Typography
          variant="body1"
          style={{
            lineHeight: "1.8",
            textShadow: "1px 1px 3px rgba(0, 0, 0, 0.3)",
          }}
        >
          Twoim zadaniem jest zaprojektowanie i zrealizowanie systemu informatycznego
          dla dziekanatu Politechniki Warszawskiej. Projekt zosta podzielony na pi
          etapw, a kady etap skada si z zada. Zaplanowae koszty i czas
          potrzebny na realizacj poszczeglnych zada, jednak to jedynie zaoenia. <br /><br />
          Twoim celem jest ukoczenie wszystkich zada w ramach ustalonego budetu i harmonogramu.
          W trakcie realizacji projektu pojawi si nieprzewidziane zadania, a wyniku podjtych decyzji mog wystpi problemy,
          wymagajce rozwizania. Kada decyzja wpynie na przebieg projektu:
          koszty, czas realizacji oraz satysfakcj klienta.
        </Typography>
      </Box>

      <Divider style={{ width: "80%", margin: "20px 0", backgroundColor: "#fff" }} />

      {/* Sekcja: Instrukcja */}
      <Box style={{ maxWidth: "800px", marginBottom: "30px" }}>
        <Typography
          variant="h5"
          gutterBottom
          style={{
            fontWeight: "bold",
            marginBottom: "10px",
            textShadow: "1px 1px 3px rgba(0, 0, 0, 0.3)",
          }}
        >
          Instrukcja
        </Typography>
        <Typography
          variant="body1"
          style={{
            lineHeight: "1.8",
            textShadow: "1px 1px 3px rgba(0, 0, 0, 0.3)",
          }}
        >
          Zadania realizujesz poprzez kliknicie w odpowiedni decyzj w prawym dolnym
          rogu ekranu. Problemy pojawiaj si po prawej stronie ekranu. Aby je rozwiza,
          kliknij na problem, a nastpnie wybierz decyzj. <br />
          Wykresy mona zwija, aby uzyska lepszy widok na inne elementy interfejsu. <br />
		  Aby wywietli ten ekran ponownie, odwie stron.
        </Typography>
      </Box>

      <Button
        variant="contained"
        style={{
          backgroundColor: "#ff9800",
          color: "#fff",
          fontWeight: "bold",
          padding: "10px 20px",
          fontSize: "18px",
          borderRadius: "8px",
          boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.3)",
          transition: "transform 0.2s, box-shadow 0.2s",
        }}
        onClick={onStart} // Wywoanie funkcji przekazanej w props
        onMouseEnter={(e) => {
          e.target.style.transform = "scale(1.05)";
          e.target.style.boxShadow = "0px 6px 15px rgba(0, 0, 0, 0.4)";
        }}
        onMouseLeave={(e) => {
          e.target.style.transform = "scale(1)";
          e.target.style.boxShadow = "0px 4px 10px rgba(0, 0, 0, 0.3)";
        }}
      >
        Start
      </Button>
    </Box>
  );
};

export default StartScreen;
